.tasks {
    width: 100%;
}

.tasks h3{
    margin-top: 50px;
    text-align: center;
}

.task_el {
    padding: 15px 10px;
    max-width: 374px;
    height: 85px;
    width: 90%;
    margin: 0 auto;
    justify-content: space-between;
    display: flex;
    align-items: center;
    box-shadow: 0 0 10px 0 rgba(0,0,0,.1);
    border-radius: 10px;
}
.task_el:first-child {
    margin-top: 10px;
 }
.task_el:not(:last-child) {
    margin-bottom: 10px;
}

.task_el .left p{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;

    padding-bottom: 5px;
    max-width: 200px;
    width: 100%;
    min-height: 10px;
    height: 100%;
}

.task_el .left span {
    background-color: #FCEEF5;
    border-radius: 3px;
}

.task_el .right {
    color: #F25F4C;
}
.task_el .right svg:hover {
    transition: .3s linear;
    color: #ff9183;
}
.task_el .right svg:first-child {
    margin-right: 5px;
}

.change_task {
    transform: translateY(-31px);
    max-width: 364px;
    height: 85px;
    width: 80%;
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    align-items: center;
    background-color: white;
    box-shadow: 0 0 10px 0 rgba(0,0,0,.1);
    border-radius: 10px;
    padding-left: 10px;
    padding-right: 10px;
}

.change_task input {
    max-width: 160px;
}
.change_task button {
    font-size: 20px;

}